.Home {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  margin: 20px;
  background: rgba(62, 76, 10, 0.792);
  border-radius: 5px;
}

.Home .left {
  width: 45%;
  text-align: left;
  font-family: cursive;
}

.Home .left p {
  padding-left: 10px;
  font-family: 'Square Peg';
  font-size: 35px;
}

.Home .right img {
  width: 200px;
  padding: 50px;
}

.Home .dots {
  color: #606c38;
}
