.About {
  display: flex;
  flex-direction: column;
  height: 70%;
  justify-content: space-evenly;
  h3 {
      width: 70%;
      align-self: center;
      padding: 10px;
    text-shadow: 2px 2px 5px #a4ee12, -2px -2px 5px #6c6d6a;
    font-family: 'Square Peg';
    font-size: 30px;
    margin: 0;
  }
  .images {
    display: flex;
    flex-flow: wrap;
    justify-content: space-evenly;
    align-items: center;
    img {
      align-items: center;
      width: 200px;
    }
    img:nth-last-child(1) {
      width: 300px;
    }
  }
}
