.Navbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-left: 40px;
  margin-right: 40px;
  border-bottom: 1px solid #283618;
}

.Navbar .Logo {
  margin-top: 0;
}

.Navbar .Logo span {
  font-size: 48px;
  color: #283618;
  position: relative;
  top: 8px;
}

.Navbar ul {
  list-style-type: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.Navbar ul li button {
  color: #fefae0;
  padding: 8px;
  background: #283618;
  margin-right: 30px;
  font-size: 16px;
  cursor: pointer;
  border: none;
}

.Navbar ul li button:hover {
  -webkit-box-shadow: 0 0 10px 5px #283618;
          box-shadow: 0 0 10px 5px #283618;
}

.Navbar ul li button:active {
  -webkit-box-shadow: 0 0 10px 1px #283618;
          box-shadow: 0 0 10px 1px #283618;
}

.Navbar ul li:nth-child(2) button {
  color: #606c38;
  background: #fefae0 !important;
}

.Navbar ul li:nth-child(2) button:hover {
  -webkit-box-shadow: 0 0 5px 5px #fefae0;
          box-shadow: 0 0 5px 5px #fefae0;
}

.Navbar ul li:nth-child(2) button:active {
  -webkit-box-shadow: 0 0 10px 1px #fefae0;
          box-shadow: 0 0 10px 1px #fefae0;
}
